'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Progress } from '@/components/ui/progress'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'

// Datos de ejemplo para el dashboard
const mockData = {
  balance_total: 125000,
  monthly_summary: {
    ingresos: 85000,
    gastos: 62000,
    balance: 23000
  },
  accounts_summary: {
    total_accounts: 3,
    accounts: [
      { id: 1, name: 'Cuenta Corriente', type: 'banco', balance: 75000, color: '#3B82F6' },
      { id: 2, name: 'Efectivo', type: 'efectivo', balance: 25000, color: '#10B981' },
      { id: 3, name: 'Ahorros', type: 'ahorros', balance: 25000, color: '#8B5CF6' }
    ]
  },
  goals_summary: {
    total_active_goals: 2,
    goals: [
      { id: 1, name: 'Vacaciones', progress: 65, current_amount: 65000, target_amount: 100000 },
      { id: 2, name: 'Fondo Emergencia', progress: 40, current_amount: 80000, target_amount: 200000 }
    ]
  },
  recent_transactions: [
    { id: 1, amount: 5000, type: 'gasto', description: 'Supermercado', date: '2024-01-15', category: { name: 'Alimentación', color: '#EF4444' } },
    { id: 2, amount: 85000, type: 'ingreso', description: 'Salario', date: '2024-01-01', category: { name: 'Salario', color: '#10B981' } },
    { id: 3, amount: 15000, type: 'gasto', description: 'Gasolina', date: '2024-01-14', category: { name: 'Transporte', color: '#F97316' } }
  ]
}

export default function Dashboard() {
  const [data, setData] = useState(mockData)

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('es-CL', {
      style: 'currency',
      currency: 'CLP'
    }).format(amount)
  }

  const getTransactionColor = (type: string) => {
    return type === 'ingreso' ? 'text-green-600' : 'text-red-600'
  }

  const getTransactionSign = (type: string) => {
    return type === 'ingreso' ? '+' : '-'
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center">
              <h1 className="text-2xl font-bold text-gray-900">Finanzas Personales</h1>
            </div>
            <nav className="hidden md:flex space-x-8">
              <Button variant="ghost" className="text-gray-600 hover:text-gray-900">
                Dashboard
              </Button>
              <Button variant="ghost" className="text-gray-600 hover:text-gray-900">
                Transacciones
              </Button>
              <Button variant="ghost" className="text-gray-600 hover:text-gray-900">
                Cuentas
              </Button>
              <Button variant="ghost" className="text-gray-600 hover:text-gray-900">
                Metas
              </Button>
              <Button variant="ghost" className="text-gray-600 hover:text-gray-900">
                Reportes
              </Button>
            </nav>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Balance Total */}
        <div className="mb-8">
          <Card className="bg-gradient-to-r from-blue-600 to-blue-700 text-white">
            <CardHeader>
              <CardTitle className="text-2xl">Balance Total</CardTitle>
              <CardDescription className="text-blue-100">
                Tu patrimonio actual
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="text-4xl font-bold mb-2">
                {formatCurrency(data.balance_total)}
              </div>
              <div className="flex items-center space-x-4 text-sm">
                <span className="text-green-200">
                  ↗ Ingresos: {formatCurrency(data.monthly_summary.ingresos)}
                </span>
                <span className="text-red-200">
                  ↘ Gastos: {formatCurrency(data.monthly_summary.gastos)}
                </span>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Resumen Mensual */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <Card className="bg-gradient-to-br from-green-50 to-green-100 border-green-200">
            <CardHeader className="pb-3">
              <CardTitle className="text-green-800">Ingresos del Mes</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-700">
                {formatCurrency(data.monthly_summary.ingresos)}
              </div>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-red-50 to-red-100 border-red-200">
            <CardHeader className="pb-3">
              <CardTitle className="text-red-800">Gastos del Mes</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-red-700">
                {formatCurrency(data.monthly_summary.gastos)}
              </div>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-blue-50 to-blue-100 border-blue-200">
            <CardHeader className="pb-3">
              <CardTitle className="text-blue-800">Balance Mensual</CardTitle>
            </CardHeader>
            <CardContent>
              <div className={`text-2xl font-bold ${data.monthly_summary.balance >= 0 ? 'text-blue-700' : 'text-red-700'}`}>
                {formatCurrency(data.monthly_summary.balance)}
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Contenido Principal */}
        <Tabs defaultValue="overview" className="space-y-6">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="overview">Resumen</TabsTrigger>
            <TabsTrigger value="accounts">Cuentas</TabsTrigger>
            <TabsTrigger value="goals">Metas</TabsTrigger>
            <TabsTrigger value="transactions">Transacciones</TabsTrigger>
          </TabsList>

          <TabsContent value="overview" className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {/* Cuentas */}
              <Card>
                <CardHeader>
                  <CardTitle>Mis Cuentas</CardTitle>
                  <CardDescription>
                    {data.accounts_summary.total_accounts} cuentas activas
                  </CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  {data.accounts_summary.accounts.map((account) => (
                    <div key={account.id} className="flex items-center justify-between p-3 rounded-lg bg-gray-50">
                      <div className="flex items-center space-x-3">
                        <div 
                          className="w-4 h-4 rounded-full"
                          style={{ backgroundColor: account.color }}
                        />
                        <div>
                          <p className="font-medium">{account.name}</p>
                          <p className="text-sm text-gray-500 capitalize">{account.type}</p>
                        </div>
                      </div>
                      <div className="text-right">
                        <p className="font-semibold">{formatCurrency(account.balance)}</p>
                      </div>
                    </div>
                  ))}
                </CardContent>
              </Card>

              {/* Metas */}
              <Card>
                <CardHeader>
                  <CardTitle>Metas de Ahorro</CardTitle>
                  <CardDescription>
                    {data.goals_summary.total_active_goals} metas activas
                  </CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  {data.goals_summary.goals.map((goal) => (
                    <div key={goal.id} className="space-y-2">
                      <div className="flex justify-between items-center">
                        <h4 className="font-medium">{goal.name}</h4>
                        <Badge variant="secondary">{goal.progress}%</Badge>
                      </div>
                      <Progress value={goal.progress} className="h-2" />
                      <div className="flex justify-between text-sm text-gray-600">
                        <span>{formatCurrency(goal.current_amount)}</span>
                        <span>{formatCurrency(goal.target_amount)}</span>
                      </div>
                    </div>
                  ))}
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          <TabsContent value="accounts">
            <Card>
              <CardHeader>
                <CardTitle>Gestión de Cuentas</CardTitle>
                <CardDescription>
                  Administra tus cuentas bancarias y de efectivo
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="text-center py-8">
                  <p className="text-gray-500 mb-4">Funcionalidad en desarrollo</p>
                  <Button>Agregar Nueva Cuenta</Button>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="goals">
            <Card>
              <CardHeader>
                <CardTitle>Metas de Ahorro</CardTitle>
                <CardDescription>
                  Define y sigue el progreso de tus objetivos financieros
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="text-center py-8">
                  <p className="text-gray-500 mb-4">Funcionalidad en desarrollo</p>
                  <Button>Crear Nueva Meta</Button>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="transactions">
            <Card>
              <CardHeader>
                <CardTitle>Transacciones Recientes</CardTitle>
                <CardDescription>
                  Últimos movimientos en tus cuentas
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {data.recent_transactions.map((transaction) => (
                    <div key={transaction.id} className="flex items-center justify-between p-3 rounded-lg bg-gray-50">
                      <div className="flex items-center space-x-3">
                        <div 
                          className="w-3 h-3 rounded-full"
                          style={{ backgroundColor: transaction.category.color }}
                        />
                        <div>
                          <p className="font-medium">{transaction.description}</p>
                          <p className="text-sm text-gray-500">{transaction.category.name}</p>
                        </div>
                      </div>
                      <div className="text-right">
                        <p className={`font-semibold ${getTransactionColor(transaction.type)}`}>
                          {getTransactionSign(transaction.type)}{formatCurrency(transaction.amount)}
                        </p>
                        <p className="text-sm text-gray-500">{transaction.date}</p>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </main>
    </div>
  )
}
